ost) }}</span>\n                </div>\n\n                <div class=\"col-12 mt-3 fw-light\">\n                  <span class=\"\">{{ $t('BigCart.count') }} {{ find(product.id).count }}</span>\n                </div>\n\n                <div class=\"col-12\">\n                  <button @click=\"remove(product.id)\" class=\"btn bg-transparent px-0 py-3 mt-2 remove-item\">{{ $t('BigCart.remove') }}</button>\n                </div>\n              </div>\n            </div>\n          </div>\n        </div>\n      </div>\n\n\n    </div>\n  </transition>\n</template>\n\n<script>\nimport {mapActions, mapGetters, mapState} from \"vuex\";\n\nexport default {\n  name: \"BigCart\",\n  emits: ['switchedOpened'],\n  data: () => ({\n    opened: false\n  }),\n  computed: {\n    ...mapGetters('cart', {\n      response_products: \"response_products\"\n    }),\n    ...mapState('i18n', {\n      locale: 'locale'\n    }),\n    ...mapState('currencies', {\n      currency: 'currency'\n    }),\n    ...mapGetters('cart', {\n      find: 'find'\n    })\n  },\n  methods: {\n    switchOpened() {\n      this.opened = !this.opened\n      this.$emit('switchedOpened', this.opened)\n    },\n    cost (cost) {\n      cost = cost * this.currency.value\n      cost = cost.toFixed(0)\n      return new Intl.NumberFormat('ru-RU').format(cost)\n    },\n    costWithPerfics (cost) {\n      return this.cost(cost) + ' ' + this.currency.symbol\n    },\n    ...mapActions('cart', {\n      remove: 'removeProduct'\n    })\n  }\n}\n</script>\n\n<style scoped>\n.nested-enter-active, .nested-leave-active {\n  transition: all 0.3s ease-in-out;\n}\n\n/* delay leave of parent element */\n.nested-leave-active {\n  transition-delay: .1s;\n}\n\n.nested-enter-from,\n.nested-leave-to {\n  transform: translateX(200px);\n  opacity: 0;\n}\n\n/* we can also transition nested elements using nested selectors */\n.nested-enter-active .inner,\n.nested-leave-active .inner {\n  transition: all 1s ease-in-out;\n}\n\n/* delay enter of nested element */\n.nested-enter-active .inner {\n  transition-delay: 10s;\n}\n\n.nested-enter-from .inner,\n.nested-leave-to .inner {\n  transform: translateX(30px);\n  /*\n  \tHack around a Chrome 96 bug in handling nested opacity transitions.\n    This is not needed in other browsers or Chrome 99+ where the bug\n    has been fixed.\n  */\n  opacity: 0.001;\n}\n</style>"],"sourceRoot":""}]);
